nav {
	background: rgba(0, 0, 0, 0.3);
	width: max-content;
	display: block;
	padding: 0.7rem 1.7rem;
	z-index: 100;
	position: fixed;
	left: 50%;
	transform: translateX(-50%);
	bottom: 2rem;
	display: flex;
	gap: 0.8rem;
	border-radius: 3rem;
	backdrop-filter: blur(15px);
	-webkit-backdrop-filter: blur(15px);
}

nav a {
	background: transparent;
	padding: 0.9rem;
	border-radius: 50%;
	display: flex;
	color: var(--color-light);
	font-size: 1.1rem;
}

nav a:hover {
	background: rgba(0, 0, 0, 0.3);
}

nav a.active {
	background: var(--color-bg);
	color: var(--color-white);
}

nav a.home {
	background: var(--color-primary);
	color: var(--color-bg);
}

nav a.about {
	background: var(--color-green);
	color: var(--color-bg);
}

nav a.portfolio {
	background: var(--color-yellow);
	color: var(--color-bg);
}

nav a.contact {
	background: var(--color-purple);
	color: var(--color-bg);
}
