@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;500;600&display=swap);
* {
	margin: 0;
	padding: 0;
	border: 0;
	outline: 0;
	box-sizing: border-box;
	list-style: none;
	text-decoration: none;
}

:root {
	--color-bg: #1a1b26;
	/* --color-bg-variant: #2c2c6c; */
	--color-bg-variant: #4e5173;
	/* --color-primary: #4db5ff; */
	--color-primary: #7aa2f7;
	/* --color-primary-variant: rgba(77, 181, 255, 0.4); */
	--color-primary-variant: rgba(122, 162, 247, 0.7);
	--color-green: #9ece6a;
	--color-red: #f7768e;
	--color-yellow: #e0af68;
	--color-cyan: #7aa2f7;
	--color-purple: #9a7ecc;
	--color-grey: #acb0d0;
	--color-white: #fff;
	--color-light: rgba(255, 255, 255, 0.6);

	--container-width-lg: 75%;
	--container-width-md: 86%;
	--container-width-sm: 90%;

	--transition: all 400ms ease;
}

html {
	scroll-behavior: smooth;
}

::-webkit-scrollbar {
	display: none;
}

body {
	font-family: 'Poppins', sans-serif;
	background-color: #1a1b26;
	background-color: var(--color-bg);
	color: #fff;
	color: var(--color-white);
	line-height: 1.7;
	background-image: url(data:image/png;base64,);
}

/* ======== General Styles ======== */
.container {
	width: 75%;
	width: var(--container-width-lg);
	margin: 0 auto;
}

h1,
h2,
h3,
h4,
h5 {
	font-weight: 500;
}

h1 {
	font-size: 2.5rem;
}

section {
	margin-top: 8rem;
	/* height: 100vh; */
}

section > h2,
section > h5 {
	text-align: center;
	color: rgba(255, 255, 255, 0.6);
	color: var(--color-light);
}

section > h2 {
	color: #7aa2f7;
	color: var(--color-primary);
	margin-bottom: 3rem;
}

.text-light {
	color: rgba(255, 255, 255, 0.6);
	color: var(--color-light);
}

a {
	color: #7aa2f7;
	color: var(--color-primary);
	transition: all 400ms ease;
	transition: var(--transition);
}

a:hover {
	color: #fff;
	color: var(--color-white);
}

.btn {
	width: -webkit-max-content;
	width: max-content;
	display: inline-block;
	color: #7aa2f7;
	color: var(--color-primary);
	padding: 0.75rem 1.2rem;
	border-radius: 0.4rem;
	cursor: pointer;
	/* border: 1px solid var(--color-primary); */
	transition: all 400ms ease;
	transition: var(--transition);
}

.btn:hover {
	background: #acb0d0;
	background: var(--color-grey);
	color: #1a1b26;
	color: var(--color-bg);
	border-color: transparent;
}

.btn-primary {
	background: #7aa2f7;
	background: var(--color-primary);
	color: #1a1b26;
	color: var(--color-bg);
}

.btn-success {
	background: #9ece6a;
	background: var(--color-green);
	color: #1a1b26;
	color: var(--color-bg);
}

img {
	display: block;
	width: 100%;
	object-fit: cover;
}

/* =========== Media Queryies (Medium Devices)============ */
@media screen and (max-width: 1024px) {
	.container {
		width: 86%;
		width: var(--container-width-md);
	}
	section {
		margin-top: 6rem;
	}
}

/* =========== Media Queryies (Small Devices)============ */
@media screen and (max-width: 600px) {
	.container {
		width: 90%;
		width: var(--container-width-sm);
	}
	section > h2 {
		margin-top: 2rem;
	}
}

header {
	height: 102vh;
	padding-top: 7rem;
	overflow: hidden;
}

.header__container {
	text-align: center;
	height: 100%;
	position: relative;
}

/* ====================== CTA =========================== */

.cta {
	margin-top: 2.5rem;
	display: flex;
	grid-gap: 1.2rem;
	gap: 1.2rem;
	justify-content: center;
}

/* ====================== Social =========================== */

.header__socials {
	display: flex;
	flex-direction: column;
	align-items: center;
	grid-gap: 0.8rem;
	gap: 0.8rem;
	position: absolute;
	left: 0;
	bottom: 2rem;
}

.header__socials::after {
	content: '';
	width: 2px;
	height: 2rem;
	background: var(--color-primary);
}

/* ====================== ME =========================== */

.me {
	background: linear-gradient(var(--color-primary), transparent);
	overflow: hidden;
	/* padding: 5rem 1.5rem 1.5rem 1.5rem; */
	width: 22rem;
	height: 30rem;
	position: absolute;
	left: calc(50% - 11rem);
	margin-top: 2rem;
	border-radius: 12rem 12rem 0 0;
}

/* ====================== SCROLL DOWN =========================== */

.scroll__down {
	position: absolute;
	right: -2.3rem;
	bottom: 5rem;
	-webkit-transform: rotate(90deg);
	        transform: rotate(90deg);
	font-weight: 300;
	font-size: 0.9rem;
}

/* =========== Media Queryies (Medium Devices)============ */
@media screen and (max-width: 1024px) {
	header {
		/* height: 64vh; */
		height: 58vh;
	}
}

/* =========== Media Queryies (Small Devices)============ */
@media screen and (max-width: 600px) {
	header {
		height: 74vh;
	}
	.scroll__down,
	.header__socials {
		display: none;
	}
}

nav {
	background: rgba(0, 0, 0, 0.3);
	width: -webkit-max-content;
	width: max-content;
	display: block;
	padding: 0.7rem 1.7rem;
	z-index: 100;
	position: fixed;
	left: 50%;
	-webkit-transform: translateX(-50%);
	        transform: translateX(-50%);
	bottom: 2rem;
	display: flex;
	grid-gap: 0.8rem;
	gap: 0.8rem;
	border-radius: 3rem;
	backdrop-filter: blur(15px);
	-webkit-backdrop-filter: blur(15px);
}

nav a {
	background: transparent;
	padding: 0.9rem;
	border-radius: 50%;
	display: flex;
	color: var(--color-light);
	font-size: 1.1rem;
}

nav a:hover {
	background: rgba(0, 0, 0, 0.3);
}

nav a.active {
	background: var(--color-bg);
	color: var(--color-white);
}

nav a.home {
	background: var(--color-primary);
	color: var(--color-bg);
}

nav a.about {
	background: var(--color-green);
	color: var(--color-bg);
}

nav a.portfolio {
	background: var(--color-yellow);
	color: var(--color-bg);
}

nav a.contact {
	background: var(--color-purple);
	color: var(--color-bg);
}

.about__container {
	display: grid;
	grid-template-columns: 30% 60%;
	grid-gap: 10%;
	gap: 10%;
}

.about__me {
	width: 100%;
	aspect-ratio: 1/1;
	border-radius: 2rem;
	/* background: linear-gradient( */
	/* 	45deg, */
	/* 	var(--color-green), */
	/* 	var(--color-primary), */
	/* 	var(--color-green) */
	/* ); */
	background: linear-gradient(
		45deg,
		var(--color-primary),
		var(--color-green),
		var(--color-primary)
	);
	display: grid;
	place-items: center;
}

.about__me-image {
	border-radius: 2rem;
	overflow: hidden;
	-webkit-transform: rotate(20deg);
	        transform: rotate(20deg);
	transition: var(--transition);
}

.about__me-image:hover {
	-webkit-transform: rotate(0deg);
	        transform: rotate(0deg);
}

.about__cards {
	display: grid;
	grid-template-columns: repeat(3, 1fr);
	grid-gap: 1rem;
	gap: 1rem;
}

.about__card {
	background: var(--color-green);
	color: var(--color-bg);
	border: 1px solid transparent;
	border-radius: 1rem;
	padding: 2rem;
	text-align: center;
	transition: var(--transition);
}

.about__card:hover {
	background: rgba(158, 206, 106, 0.8);
	cursor: default;
}

.about__icon {
	font-size: 1.4rem;
	margin-bottom: 1rem;
}

.about__card h5 {
	font-size: 0.9rem;
}

.about__card small {
	font-size: 0.7rem;
}

.about__content p {
	margin: 2rem 0 2.6rem;
	color: var(--color-light);
}

/* =========== Media Queryies (Medium Devices)============ */
@media screen and (max-width: 1024px) {
	.about__container {
		grid-template-columns: 1fr;
		grid-gap: 0;
		gap: 0;
	}

	.about__me {
		width: 55%;
		margin: 2rem auto 4rem;
	}

	.about__content {
		text-align: center;
	}
	.about__content p {
		margin: 1rem 1.5rem;
	}
}

/* =========== Media Queryies (Small Devices)============ */
@media screen and (max-width: 600px) {
	.about__me {
		width: 65%;
		margin: 1rem auto 3rem;
	}

	.about__cards {
		grid-template-columns: 1fr 1fr;
		grid-gap: 1rem;
		gap: 1rem;
	}

	.about__content {
		text-align: center;
	}

	.about__content p {
		margin: 1.5rem 0;
	}
}

.portfolio__container {
	display: grid;
	grid-template-columns: repeat(3, 1fr);
	grid-gap: 2.5rem;
	gap: 2.5rem;
	margin-top: -90;
}

.portfolio__item {
	/* background: var(--color-bg-variant); */
	background: var(--color-primary);
	padding: 1rem;
	border-radius: 1rem;
	border: 1px solid transparent;
	transition: var(--transition);
	color: var(--color-bg);
}

.portfolio__item:hover {
	background: var(--color-primary-variant);
	/* background: rgba(44, 44, 108, 0.5); */
	/* color: var(--color-white); */
}

.portfolio__item-image {
	border-radius: 1.1rem;
	overflow: hidden;
}

.portfolio__item h3 {
	margin: 1.2rem 0 1.8rem;
}

.portfolio__item-cta {
	display: flex;
	margin-bottom: 1rem;
	text-align: center;
}

/* =========== Media Queryies (Medium Devices)============ */
@media screen and (max-width: 1024px) {
	.portfolio__container {
		grid-template-columns: 1fr 1fr;
		grid-gap: 1.2rem;
		gap: 1.2rem;
	}
}

/* =========== Media Queryies (Small Devices)============ */
@media screen and (max-width: 600px) {
	.portfolio__container {
		grid-template-columns: 1fr;
		grid-gap: 1rem;
		gap: 1rem;
	}
}

.container.contact__container {
	width: 58%;
	display: grid;
	grid-template-columns: 30% 58%;
	grid-gap: 8rem;
	gap: 8rem;
}

.contact__options {
	display: flex;
	flex-direction: column;
	grid-gap: 1.2rem;
	gap: 1.2rem;
}

.contact__option {
	background: var(--color-primary);
	color: var(--color-bg);
	padding: 1.2rem;
	border-radius: 1.2rem;
	text-align: center;
	transition: var(--transition);
}

.contact__option a {
	margin-top: 0.7rem;
	display: inline-block;
	font-size: 0.8rem;
	color: var(--color-white);
}

.contact__option:hover {
	background: var(--color-primary-variant);
	color: var(--color-bg);
}

.contact__option-icon {
	font-size: 1.8rem;
}

/* ========================== FORM ======================= */

form {
	display: flex;
	flex-direction: column;
	grid-gap: 1.2rem;
	gap: 1.2rem;
}

input,
textarea {
	width: 100%;
	padding: 1.5rem;
	border-radius: 0.5rem;
	background: var(--color-bg-variant);
	resize: none;
	color: var(--color-white);
	font-family: monospace;
}

::-webkit-input-placeholder {
	color: var(--color-white);
	font-family: monospace;
}

::placeholder {
	color: var(--color-white);
	font-family: monospace;
}

.block {
	width: 100%;
	display: block;
	padding: 14px 28px;
	cursor: pointer;
}

/* =========== Media Queryies (Medium Devices)============ */
@media screen and (max-width: 1024px) {
	.container.contact__container {
		grid-template-columns: 1fr;
		grid-gap: 2rem;
		gap: 2rem;
	}
}

/* =========== Media Queryies (Small Devices)============ */
@media screen and (max-width: 600px) {
	.container.contact__container {
		width: var(--container-width-sm);
	}
}

footer {
	display: grid;
	grid-template-columns: 33% 33% 33%;
	background: var(--color-primary);
	padding: 3rem 0;
	text-align: center;
	font-size: 0.9rem;
	margin-top: 7rem;
}

footer a {
	color: var(--color-bg);
}

.footer__logo {
	font-size: 2rem;
	font-weight: 500;
	margin-bottom: 2rem;
	display: inline-block;
}

.permalinks {
	display: flex;
	flex-wrap: wrap;
	justify-content: center;
	grid-gap: 2rem;
	gap: 2rem;
	margin: 0 auto 3rem;
}

.footer__socials {
	display: flex;
	flex-wrap: wrap;
	justify-content: center;
	grid-gap: 1rem;
	gap: 1rem;
	margin-bottom: 4rem;
}

.footer__socials a {
	background: var(--color-bg);
	color: var(--color-white);
	padding: 0.8rem;
	border-radius: 0.7rem;
	display: flex;
	border: 1px solid transparent;
}

.footer__socials a:hover {
	background: transparent;
	color: var(--color-bg);
	border-color: var(--color-bg);
}

.footer__copyright {
	color: var(--color-bg);
	font-size: 1.2rem;
}

