.about__container {
	display: grid;
	grid-template-columns: 30% 60%;
	gap: 10%;
}

.about__me {
	width: 100%;
	aspect-ratio: 1/1;
	border-radius: 2rem;
	/* background: linear-gradient( */
	/* 	45deg, */
	/* 	var(--color-green), */
	/* 	var(--color-primary), */
	/* 	var(--color-green) */
	/* ); */
	background: linear-gradient(
		45deg,
		var(--color-primary),
		var(--color-green),
		var(--color-primary)
	);
	display: grid;
	place-items: center;
}

.about__me-image {
	border-radius: 2rem;
	overflow: hidden;
	transform: rotate(20deg);
	transition: var(--transition);
}

.about__me-image:hover {
	transform: rotate(0deg);
}

.about__cards {
	display: grid;
	grid-template-columns: repeat(3, 1fr);
	gap: 1rem;
}

.about__card {
	background: var(--color-green);
	color: var(--color-bg);
	border: 1px solid transparent;
	border-radius: 1rem;
	padding: 2rem;
	text-align: center;
	transition: var(--transition);
}

.about__card:hover {
	background: rgba(158, 206, 106, 0.8);
	cursor: default;
}

.about__icon {
	font-size: 1.4rem;
	margin-bottom: 1rem;
}

.about__card h5 {
	font-size: 0.9rem;
}

.about__card small {
	font-size: 0.7rem;
}

.about__content p {
	margin: 2rem 0 2.6rem;
	color: var(--color-light);
}

/* =========== Media Queryies (Medium Devices)============ */
@media screen and (max-width: 1024px) {
	.about__container {
		grid-template-columns: 1fr;
		gap: 0;
	}

	.about__me {
		width: 55%;
		margin: 2rem auto 4rem;
	}

	.about__content {
		text-align: center;
	}
	.about__content p {
		margin: 1rem 1.5rem;
	}
}

/* =========== Media Queryies (Small Devices)============ */
@media screen and (max-width: 600px) {
	.about__me {
		width: 65%;
		margin: 1rem auto 3rem;
	}

	.about__cards {
		grid-template-columns: 1fr 1fr;
		gap: 1rem;
	}

	.about__content {
		text-align: center;
	}

	.about__content p {
		margin: 1.5rem 0;
	}
}
